@import "partials/variables";
@import 'partials/mixins';

$success: $color-success; // green
$alert: $color-danger;  // red

// fonts
$primary-font: 'Merriweather', serif;
$secondary-font: 'Open Sans', sans-serif;
$logoSymbolColor:#fff;
$logo:url('data:image/svg+xml;utf8,<svg xmlns:svg="http://www.w3.org/2000/svg" xmlns="http://www.w3.org/2000/svg" width="88" height="88" viewBox="0 0 313 313" version="1"><path d="m111 204 0-90" style="fill:none;stroke-linecap:round;stroke-linejoin:round;stroke-opacity:0.24;stroke-width:32.4;stroke:#000"/><path d="m111 200 0-90" style="stroke-linecap:round;stroke-linejoin:round;stroke-width:32.4;stroke:#{$logoSymbolColor}"/><path d="m157 160c-23 0-46-22-46-46 0-25 22-45 46-45 25 0 45 20 45 45l0 137" style="fill:none;stroke-linecap:round;stroke-linejoin:round;stroke-opacity:0.24;stroke-width:32.4;stroke:#000"/><circle r="16" cy="252" cx="111" style="fill-opacity:0.24;fill:#000"/><path d="m157 156c-23 0-46-22-46-46 0-25 22-45 46-45 25 0 45 20 45 45l0 137" style="fill:none;stroke-linecap:round;stroke-linejoin:round;stroke-width:32.4;stroke:#{$logoSymbolColor}"/><circle cx="111" cy="248" r="16" fill="#{$logoSymbolColor}"/></svg>');
.ui-nav {
	width: 100%;
	top: 0;
	left: 0;
	background:$color-primary;
	z-index:11;
	.is-brand {
		a{
			background:$color-primary $logo no-repeat center center;
			background-size:contain;
			text-indent: -9999px;
			display: inline-block;
			width:50px;
			height:50px;
		}
	}
	.navbar {
	    padding: 0rem 2rem;
	}
	a {
	    padding: 1.4rem 2rem;
	    margin-bottom: 0px;
	}
	.container {
	    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
	}
}

main.container {
    width: 90%;
}

/* --- xPatterns --- */
pre.box {
    code{
    	font-size:100%;
    }
}
div.box.block {
    //border: 1px solid #E4D523;
    padding: 15px!important;
    //border-width: 4px 1px 1px;
}
.typo{
	display:inline-block;
	margin-top:0;
}
.inline-el{
    a,
    em,
    small,
    strong,
    u{
    	display:inline-block;
    	margin:2rem 4rem 4rem 0;
    }
    blockquote{
    	small{
    		margin: 0;
    	}
    }
}

/* --- xTypography --- */
h2.box-title {
	font-weight: 200;
	text-transform: uppercase;
}


/* --- xGrid --- */
.show-grid{
	
	div[class^=col-],
	div[class^=col] {
	    padding-top: 10px;
	    padding-bottom: 10px;
		background-color: #E2E2E2;
		border: 1px solid #fff;
		border-radius: 3px;
		text-align:center;
		overflow: hidden;
		text-overflow: ellipsis;
	}
}
.show-grid .row div[class^=col]:nth-child(odd){
	background: $gray-lighter;
}
.show-grid div[class^=col-],.show-grid .row div[class^=col]:first-child {
    background: $color-success;
    color:#fff;
}
/* --- xButtons --- */
.box-buttons pre span {
	/* CSS class name color */ 
	color: $color-primary;
}
.on-sidelines{
	.box.block{
		padding: 1em 1em 1em 0;
	}
}
.box.block {
    padding: 1em 0 0;
    margin-bottom:0;
}

@import 'partials/basic-style';